import { BASE_IMAGE_URL,pay,H5login,quotationsToOrder,orderStaus} from "@/utils/api";
import weixin_sdk from '@/utils/weixin-jsapi.js';
import {isWeiXin} from "@/utils/index.js"

export default {
  data () {
    return {
      global: {},
      name: '',
      mobile: '',
      address: '',
      items: [
        {name: '微信支付', icon_url: BASE_IMAGE_URL+'weixin_pay.png'}
      ],
      current: 0,
      openid: '',
      licheng_order_id: '',
      jiaoqiang_order_id: '',
			showModal: false,
			monthly_expense: '',
			mileage_expense: '',
			quotation_id: 0
    }
  },

  methods: {
		fixIos: function(){
				setTimeout(function() {                
					var scrollHeight = document.documentElement.scrollTop || document.body.scrollTop || 0;
					window.scrollTo(0, Math.max(scrollHeight - 1, 0));            
				}, 100);
			},
		hideModal: function(){
			// let quotation_id = this.$root.$mp.query.quotation_id;
			// this.quotation_id = quotation_id?quotation_id:this.global.quotation_id;
			// this.showModal = false;
			// this.quotationsToOrder();
			this.showModal = false;
		},
    next () {
			this.showModal = false;
      uni.navigateTo({
        url: "../pay_one/pay_one?jiaoqiang_order_id="+this.jiaoqiang_order_id
      })
    },
		
		async orderStaus(){
			let params = {
				id: this.licheng_order_id
			};
			let res = await orderStaus(params);
			if(res.code == 200){
				if(res.success){
					// this.hideModal();
					this.next();
				}else{
					uni.showToast({
						icon: 'none',
						title: "支付失败，请稍后重试",
						duration: 1000
					});
					this.hideModal();
					this.resetPage();
				}
			}else{
				this.resetPage();
				uni.showToast({
          icon: 'none',
          title: res.msg,
          duration: 1000
        });
				this.hideModal();
				
			}
		},
		
		radioChange: function(e){
      console.log(e.detail.value);
    },
		
		async quotationsToOrder(){
			// this.global.quotation_id
      let res = await quotationsToOrder(this.quotation_id);
      if(res.success){
        if(res.orders.length > 0){
          let licheng = res.orders[0];
          if(licheng.type == 1){
            this.licheng_order_id = licheng.id;
						this.monthly_expense = licheng.monthly_expense;
						this.mileage_expense = licheng.mileage_expense;
						this.name = licheng.name;
						this.mobile = licheng.mobile;
          };

          let jiaoqiang = res.orders[1];
          if(jiaoqiang.type == 2){
            this.jiaoqiang_order_id = jiaoqiang.id;
						this.global.compulsory = jiaoqiang.price;
            this.global.tax = jiaoqiang.tax;
						uni.setStorageSync("global", this.global);
          }
        }
      }
    },
		
		async pay(){
			let that = this;
      if(!this.address){
        wx.showToast({
          icon: 'none',
          title: '请输入地址',
          duration: 1000
        });
        return;
      }
			
			let openid = uni.getStorageSync("openid");
      let params = {
        name: this.name,
        mobile: this.mobile,
        address: this.address,
				openid: openid,
        paytype: 1,
      };
      let res = await pay(this.licheng_order_id, params);
      if(res.success){
        //this.next();
        let result = res.result;
        console.log(result);
        console.log(result.timestamp);
				
				if(!openid){
					that.showModal = true;
					window.location.href = result.mweb_url;
					return;
				}
				
				weixin_sdk.config({
						debug: false, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
						appId: 'wx49aad3a138063b53', // 必填，企业号的唯一标识，此处填写企业号corpid
						timestamp: result.timestamp, // 必填，生成签名的时间戳
						nonceStr: result.nonceStr, // 必填，生成签名的随机串
						signature: result.paySign,// 必填，签名，见附录1
						jsApiList: ['getBrandWCPayRequest'] // 必填，需要使用的JS接口列表，所有JS接口列表见附录2
				});
				
				weixin_sdk.ready(function(){
					weixin_sdk.checkJsApi({
						jsApiList: ['getBrandWCPayRequest'],
						success: res => {
							
							weixin_sdk.invoke(
							 'getBrandWCPayRequest', {
									 "appId" : 'wx49aad3a138063b53',       //公众号名称，由商户传入
									 "timeStamp":result.timestamp, //时间戳，自1970年以来的秒数     
									 "nonceStr" : result.nonceStr, //随机串     
									 "package" : result.package,     
									 "signType" :'MD5',  //微信签名方式：     
									 "paySign" : result.paySign     //微信签名 

							 },
							 function(res){ 
									 if(res.err_msg == "get_brand_wcpay_request:ok" ) {
											 wx.showToast({
													title: '支付成功',
													icon: 'none',
													duration: 1000
											 });
											 that.next();
									 }else{
										 wx.showToast({
												title: '支付失败',
												icon: 'none',
												duration: 1000
										});
									 }
							 }
						)
						},
						fail:function(res){
									console.log(res);
									wx.showToast({
											title: '支付失败',
											icon: 'none',
											duration: 1000
									});
							},
					})
				})
						
				weixin_sdk.error(function(err){
					wx.showToast({
							title: '支付失败',
							icon: 'none',
							duration: 1000
					});
				})


        
      }
    },
		
		resetPage:function(){
			this.global = wx.getStorageSync("global");
			let quotation_id = this.$root.$mp.query.quotation_id;
				this.quotation_id = quotation_id?quotation_id:this.global.quotation_id;
				this.quotationsToOrder();
		},
		
		async H5login(){
			let partner_id = this.$root.$mp.query.partner_id;
			let imei = this.$root.$mp.query.imei;
			if(!partner_id){
				partner_id = uni.getStorageSync("partner_id");
			}
			
			if(!imei){
				imei = uni.getStorageSync("imei");
			}
				let params = {
					partner_id: partner_id,
					imei: imei
				};
				
				let res = await H5login(params);
				if(res.code == 200){
					let token = res.data.token;
					console.log(token);
					if(token){
						uni.setStorageSync('token', token);
					}
					this.quotationsToOrder();
				}
			},
		
  },

  onLoad () {
    this.global = wx.getStorageSync("global");
		if(!this.global){
			wx.setStorageSync("global", {})
		}
    // this.getOpenid();
		
		let quotation_id = this.$root.$mp.query.quotation_id;
		this.quotation_id = quotation_id?quotation_id:this.global.quotation_id;
		if(this.global){
			this.name = this.global.name;
			this.mobile = this.global.mobile;
		}
		
		let openid = uni.getStorageSync("openid");
		if(!openid && isWeiXin()){
			openid = this.$root.$mp.query.open_id;
			if(!openid){
				let curUrl = window.location.href;
				openid = curUrl.split("open_id=")[1].split("#")[0];
			}
			
			uni.setStorageSync("openid", openid);
		}
		
  },
	
	onShow(){
			let iswechat = this.$root.$mp.query.iswechat;
			if(iswechat){
				let quotation_id = this.$root.$mp.query.quotation_id;
				this.quotation_id = quotation_id?quotation_id:this.global.quotation_id;
				console.log(weixin_sdk);
				this.quotationsToOrder();
				this.showModal = true;
			}else{
				uni.removeStorageSync("pay_address");
				//this.H5login();
				this.quotationsToOrder();
			}
			
			
	}

}
